import { HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { LayoutComponent } from './core/layout/layout.component';
import { LandingComponent } from './landing/landing.component';
import { MaterialModule } from './shared/material/material.module';
import { SurveyComponent } from './survey/survey.component';
import { TreatmentLocatorComponent } from './treatment-locator/treatment-locator.component';

import { AboutComponent } from './about/about.component';

import { MAT_RADIO_DEFAULT_OPTIONS } from '@angular/material/radio';
import { OnboardingModule } from '@rosen-group/ngx-onboarding';
import { JoyrideModule } from 'ngx-joyride';
import { HeaderComponent } from './core/header/header.component';
import { SurveyModalComponent } from './landing/modals/survey-modal/survey-modal.component';
import { SearchModule } from './search/search.module';
import { AlertModule } from './alert/alert.module';
import { RRCConfitmationDialog, TelehealthFormComponent } from './telehealth-form/telehealth-form.component';
import { BottomNavComponent } from './core/bottom-nav/bottom-nav.component';
import { SpeakerRequestConfitmationDialog, SpeakerRequestFormComponent } from './speaker-request-form/speaker-request-form.component';
import { WhatsNewModalComponent } from './whats-new-modal/whats-new-modal.component';
import { ErrorDialogComponent } from './shared/modal/error-dialog/error-dialog.component';
import { TreatmentLocatorDialog } from './treatment-locator/dialogs/treatment-locator-dialog/treatment-locator-dialog.component';
import { TreatmentProviderListItemComponent } from './treatment-locator/components/treatment-provider-list-item/treatment-provider-list-item.component';


@NgModule({
  declarations: [
    AppComponent,
    LayoutComponent,
    TreatmentLocatorComponent,
    LandingComponent,
    AboutComponent,
    SurveyComponent,
    BottomNavComponent,
    TreatmentLocatorDialog,
    SurveyModalComponent,
    HeaderComponent,
    TelehealthFormComponent,
    RRCConfitmationDialog,
    SpeakerRequestFormComponent,
    SpeakerRequestConfitmationDialog,
    WhatsNewModalComponent,
    ErrorDialogComponent,
    TreatmentProviderListItemComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    ServiceWorkerModule.register('ngsw-worker.js', {
      enabled: environment.production,
      // Register the ServiceWorker as soon as the app is stable
      // or after 30 seconds (whichever comes first).
      registrationStrategy: 'registerWhenStable:30000'
    }),
    BrowserAnimationsModule,
    MaterialModule,
    HttpClientModule,
    AlertModule,
    OnboardingModule,
    JoyrideModule.forRoot(),
    SearchModule,
    // PreventionModule
  ],
  providers: [{
    provide: MAT_RADIO_DEFAULT_OPTIONS,
    useValue: { color: 'primary' },
  }],
  bootstrap: [AppComponent]
})
export class AppModule { }
