import { animate, state, style, transition, trigger } from '@angular/animations';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';

import { Alert } from '../alert.model';
import { AlertService } from '../alert.service';

@Component({
  selector: 'alert-container',
  templateUrl: 'alert-container.component.html'
})
export class AlertContainerComponent implements OnInit, OnDestroy {
  alerts: any[] = [];
  alertSubscription!: Subscription;
  currentIndex = 0;
  currentAlert: Alert;
  showAlert: boolean = false;

  constructor(private alertService: AlertService) { }

  ngOnInit() {
    this.alertSubscription = this.alertService.fetchAlerts().subscribe(alerts => {
      this.alerts = alerts;
    })
    this.startCarousel(10);
  }

  ngOnDestroy(): void {
    this.alertSubscription.unsubscribe();
  }

  // onRemoveAlert(alert: Alert) {
  //   if (!this.alerts.includes(alert)) return;

  //   this.alerts = this.alerts.filter(x => x !== alert);
  // }

  startCarousel(seconds: number = 10): void {
    console.log("start carousel")
    var interval = seconds * 1000;
    setInterval(() => {
      this.incrementIndex();
    }, interval)
  }

  incrementIndex() {
    if (this.currentIndex >= (this.alerts.length - 1)) this.currentIndex = 0;
    else this.currentIndex++;
    this.currentAlert = this.alerts[this.currentIndex];
  }
}
