import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { SurveyService } from 'src/app/services/survey.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef } from '@angular/material/dialog';
import { Storage } from '@capacitor/storage';

@Component({
  templateUrl: './survey-modal.component.html',
  styleUrls: ['./survey-modal.component.scss']
})
export class SurveyModalComponent implements OnInit {
  reasonOptions = [
    "",
    "Family/Friend of someone in need of services or support",
    "Social Service Provider",
    "First Responder(Law Enforcement)",
    "First Responder (Fire)",
    "Fire Responder (EMS)",
    "Educator",
    "Healthcare",
    "ADMH Certified Provider",
    "Other",
  ];
  ageOptions = Array.from(Array(99).keys()).slice(1);


  agreedToTerms = new FormControl(false);
  surveyForm = new FormGroup({
    age: new FormControl(18, [Validators.required]),
    reason: new FormControl('', [Validators.required]),
    isVeteran: new FormControl(null, [Validators.required]),
    agreeTerms: new FormControl(false, [Validators.requiredTrue]),
  });

  constructor(
    public dialogRef: MatDialogRef<SurveyModalComponent>,
    private surveyService: SurveyService,
    private _snackBar: MatSnackBar
  ) { }

  ngOnInit(): void {
  }

  get age() {
    return this.surveyForm.get('age')
  }

  onSubmit(): void {
    this.surveyService.saveSurvey({
      date: new Date(),
      age: this.age.value,
      reason: this.surveyForm.get('reason').value,
      isVeteran: this.surveyForm.get('isVeteran').value
    }).subscribe(async (res) => {
      await Storage.set({
        key: 'surveyHasBeenSaved',
        value: 'true',
      });

      let snackBarRef = this._snackBar.open('Thank you for taking the time to complete the survey.', '', {
        duration: 10000,
      });
      this.dialogRef.close();
    });
  }

}
