import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import SearchIndexData from '../../assets/json/search-index.json';
import { fromSerializable, SerializableDocumentKey } from "ndx-serializable";
import { query, QueryResult } from "ndx-query";
import { addDocumentToIndex, createIndex, Index, removeDocumentFromIndex, vacuumIndex } from 'ndx';
import { words } from "lodash";

@Injectable({
  providedIn: 'root'
})
export class SearchService {
  public searchTerm: BehaviorSubject<string> = new BehaviorSubject<string>(null);
  private searchResultsSource = new BehaviorSubject<any[]>([]);
  public searchResults$ = this.searchResultsSource.asObservable();

  constructor() {

    // copy data from file to object
    const data = JSON.parse(JSON.stringify(SearchIndexData));

    // Decode from JSON, MsgPack or other format and convert from serializable.
    const indexData = fromSerializable(data);
    const index = this.createDocumentIndex(indexData)

    this.searchTerm.asObservable().subscribe(term => {
      // console.log(term);
      let searchResults = index.search(term);

      // console.log(searchResults.length, " Results Found")
      // console.log(searchResults)
      this.searchResultsSource.next(searchResults);
    })

    this.searchResults$.subscribe((results) => {
      console.log("search results: ", results)
    })
  }

  private createDocumentIndex(index: Index<SerializableDocumentKey>) {
    let filter = (term: string): string => {
      return term.toLowerCase();
    }

    const fieldBoostFactors = index.fields.map(() => 1);
    // To support removing from the index, we need to create a set of removed documents.
    const removed = new Set();

    // TODO: Set up tokenizer stemmer for filter and node index creation script
    return {
      search: (q: string) => query(index, fieldBoostFactors, 1.2, 0.75, words, filter, removed, q),
    };
  }

}

