import { CommonModule } from "@angular/common";
import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ITreatmentProvider } from "src/app/models/TreatmentProviders.models";

@Component({
  selector: 'app-treatment-provider-list-item',
  template: `
  <li class="hover:bg-gray-50 px-4 py-4 sm:px-6" (click)="showProviderEvent.emit(treatmentProvider)">
      <div class="flex items-center space-x-4">
          <div class="flex-1 min-w-0">
              <p class="text-sm font-medium text-gray-900 truncate">
                  {{treatmentProvider?.commonName || ""}}
              </p>
              <p class="text-sm text-gray-500 truncate">
                  {{CityState || ""}}
              </p>
          </div>
      </div>
      <div>
          {{treatmentProvider?.phone || ""}}
      </div>
  </li>
  `,
  styleUrls: ['./treatment-provider-list-item.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TreatmentProviderListItemComponent implements OnInit {
  @Input() treatmentProvider: ITreatmentProvider;
  @Output() showProviderEvent: EventEmitter<ITreatmentProvider> = new EventEmitter();


  CityState = "";
  ngOnInit(): void {
    if (this.treatmentProvider.city) {
      this.CityState += this.treatmentProvider.city;
      if (this.treatmentProvider.state) {
        this.CityState += ", ";
        this.CityState += this.treatmentProvider.state;
      }
    } else {
      if (this.treatmentProvider.state) {
        this.CityState += this.treatmentProvider.state;
      }
    }
  }
}
