import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { ITreatmentProvider, ITreatmentSearchQuery } from '../models/TreatmentProviders.models';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(
    private http: HttpClient
  ) { }

  getTreatmentProviders() {
    return this.http.get<ITreatmentProvider[]>(`${environment.apiUrl}/TreatmentProviders`);
  }
  searchTreatmentProviders(searchFilters: ITreatmentSearchQuery) {
    return this.http.post<ITreatmentProvider[]>(`${environment.apiUrl}/search`, searchFilters);
  }
  searchSubstanceUseProviders(searchFilters: ITreatmentSearchQuery) {
    return this.http.post<ITreatmentProvider[]>(`${environment.apiUrl}/search`, searchFilters);
  }
  searchPreventionProviders(searchFilters: ITreatmentSearchQuery) {
    return this.http.post<ITreatmentProvider[]>(`${environment.apiUrl}/search`, searchFilters);
  }
  searchMentalHealthProviders(searchFilters: ITreatmentSearchQuery) {
    return this.http.post<ITreatmentProvider[]>(`${environment.apiUrl}/search`, searchFilters);
  }
}
