import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { LayoutComponent } from './core/layout/layout.component';
import { LandingComponent } from './landing/landing.component';
import { SearchComponent } from './search/search.component';
import { AboutComponent } from './about/about.component';
import { HelplinesRoutes } from './sections/helplines/helplines.module';
import { MentalHealthRoutes } from './sections/mental-health/mental-health.module';
import { PreventionRoutes } from './sections/prevention/prevention.module';
import { SubstanceUseRoutes } from './sections/substance-use/substance-use.module';
import { SpeakerRequestFormComponent } from './speaker-request-form/speaker-request-form.component';
import { SurveyComponent } from './survey/survey.component';
import { TelehealthFormComponent } from './telehealth-form/telehealth-form.component';
import { TreatmentLocatorComponent } from './treatment-locator/treatment-locator.component';

const topLevelRoutes = [
  {
    path: '',
    component: LandingComponent,
    data: {
      id: '',
      showHeader: false,
      showAlert: true,
    }
  },
  {
    path: 'about',
    component: AboutComponent,
    data: {
      id: 'about',
      showHeader: false,
      showAlert: true,
    }
  },
  {
    path: 'telehealth',
    component: TelehealthFormComponent,
    data: {
      id: 'telehealth',
      showHeader: false,
      showAlert: false,
    }
  },
  {
    path: 'search',
    component: SearchComponent,
    data: {
      id: 'search',
      showHeader: false,
      showAlert: false
    }
  },
  {
    path: 'treatment-locator',
    component: TreatmentLocatorComponent,
    data: {
      pageTitle: 'Treatment Locator',
      pageTitleClasses: "absolute inline-block text-center justify-center w-full text-xl font-extrabold tracking-tight sm:text-2xl bg-blue-500 text-white",
      headerClasses: "z-2 justify-center text-center bg-blue-500 py-5",
      routerLink: "/",
      showHeader: false,
      showAlert: true,
    }
  },
  {
    path: 'survey',
    component: SurveyComponent,
    data: {
      id: 'survey',
      showHeader: false,
      showAlert: true,
    }
  },
  {
    path: 'training-education',
    component: SpeakerRequestFormComponent,
    data: {
      id: 'survey',
      showHeader: false,
      showAlert: false,
    }
  },
]

const routes: Routes = [
  {
    path: '',
    component: LayoutComponent,
    children: [
      ...topLevelRoutes,
      ...SubstanceUseRoutes,
      ...PreventionRoutes,
      ...MentalHealthRoutes,
      ...HelplinesRoutes
    ]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    scrollPositionRestoration: 'enabled',
    preloadingStrategy: PreloadAllModules
  })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
