import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Counties } from 'src/assets/data/Counties';
import { environment } from 'src/environments/environment';
import { ErrorDialogComponent } from '../shared/modal/error-dialog/error-dialog.component';

@Component({
  selector: 'app-telehealth-form',
  templateUrl: './telehealth-form.component.html',
  styleUrls: ['./telehealth-form.component.scss']
})
export class TelehealthFormComponent implements OnInit {
  connectALLogo = "/assets/telehealth/ConnectAL_Horizontal_Color.png";
  // ALDMH = "/assets/ADMH_LOGO_TRANPARENT.png";
  ALDMH = "/assets/ADMH_WHITE.png"
  vitallogo = "/assets/telehealth/VitAL_White.png";
  aphlogo = "/assets/telehealth/Alabama Public Health.png";
  crisisCenterLogo = "/assets/telehealth/Crisis Center.png";
  rrcLogo = "/assets/telehealth/rrc_logo.png"

  // Name(required)
  // Working phone number(required)
  // Currently using what substance?(required)
  // Currently in what county?(required)
  countyOptions = Counties;

  //
  telehealthForm = new FormGroup({
    // name open
    firstName: new FormControl('', [Validators.required]),
    lastName: new FormControl('', [Validators.required]),
    // phone number
    email: new FormControl('', [Validators.required]),
    phone: new FormControl('', [Validators.required]),
    // open or substance select
    substance: new FormControl('', [Validators.required]),
    // county select
    county: new FormControl('', [Validators.required]),
  });

  constructor(
    private dialog: MatDialog,
    private httpClient: HttpClient
  ) { }

  ngOnInit(): void {
  }

  onSubmit(): void {
    let rawForm = this.telehealthForm.value;

    let body = {
      name: rawForm.firstName + " " + rawForm.lastName,
      phone: rawForm.phone,
      substance: rawForm.substance,
      county: rawForm.county,
      email: rawForm.email
    }

    this.httpClient.post(`${environment.apiUrl}/form/telehealth`, body)
      .subscribe(result => {
        this.telehealthForm.reset();
        this.dialog.open(RRCConfitmationDialog).afterClosed();
      },
        error => {
          this.dialog.open(ErrorDialogComponent, {
            data: {
              message: "Error sending telehealth request. Please try again later."
            }
          })
        })
  }
}

@Component({
  selector: 'rrc-confirmation-dialog',
  templateUrl: './rrc-confirmation-dialog.html',
})
export class RRCConfitmationDialog implements OnInit {


  fromWebPage!: string;
  fromWebDialog!: string;

  constructor(
    public dialogRef: MatDialogRef<RRCConfitmationDialog>
  ) { }

  ngOnInit(): void { }

  modalDialogYes() {
    this.dialogRef.close({ event: "yes-prop", data: this.fromWebDialog });
  }
  modalDialogNo() {
    this.dialogRef.close({ event: "no-prop", data: this.fromWebDialog });
  }
  modalDialogMaybe() {
    this.dialogRef.close({ event: "maybe-prop", data: this.fromWebDialog });
  }
}
