import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatAccordion } from '@angular/material/expansion';
import { PageEvent } from '@angular/material/paginator';
import { MatRadioChange } from '@angular/material/radio';
import { Geolocation, Position } from '@capacitor/geolocation';
import { words, toLower, intersection } from 'lodash';
import { BehaviorSubject } from 'rxjs';
import { ITreatmentSearchQuery, ITreatmentProvider, TreatmentProviderListFilters, TreatmentType } from 'src/app/models/TreatmentProviders.models';
import { ApiService } from 'src/app/services/api.service';
import { Demographic } from 'src/assets/data/DemographicsList';
import { ServiceOffered } from 'src/assets/data/ServicesOfferedList';
import { TreatmentLocatorDialog } from './dialogs/treatment-locator-dialog/treatment-locator-dialog.component';

@Component({
  templateUrl: './treatment-locator.component.html',
  styleUrls: ['./treatment-locator.component.scss']
})
export class TreatmentLocatorComponent implements OnInit {
  searchAddress = '';
  useDeviceLocation = true;
  selectedServices$: BehaviorSubject<string[]> = new BehaviorSubject<string[]>([]);

  unfilteredList: ITreatmentProvider[] = [];
  treatmentProviderList$: BehaviorSubject<ITreatmentProvider[]> = new BehaviorSubject<ITreatmentProvider[]>([]);
  displayedTreatmentProviderList$: BehaviorSubject<ITreatmentProvider[]> = new BehaviorSubject<ITreatmentProvider[]>([]);

  // MatPaginator Inputs
  length = 0;
  pageSize = 10;
  pageSizeOptions: number[] = [5, 10, 25, 100];

  // MatPaginator Output
  pageEvent!: PageEvent;

  canUseDeviceLocation!: boolean;
  range = '25';

  searchFilters: BehaviorSubject<TreatmentProviderListFilters> = new BehaviorSubject<TreatmentProviderListFilters>({
    servicesOffered: [],
    demographics: [],
    category: undefined,
    county: null,
  });

  services = new FormControl();
  serviceList = [
    {
      label: "Co-occuring Care",
      keywords: ["medically", "monitered", "hiv", "pregnant", "co-occuring"],
    },
    {
      label: "Residential",
      keywords: ["residential", "ambulatory", "level", "i", "partial", "hospitalization", "narr"],
    },
    {
      label: "Outpatient",
      keywords: ['outpatient', 'ambulatory']
    },
    {
      label: "Detox",
      keywords: ['detox']
    },
    {
      label: "MAT",
      keywords: ['medically', 'monitered', 'opioid', 'maintenance']
    },
    {
      label: "Recovery Housing",
      keywords: ['housing']
    },
    {
      label: "Faith Based Services",
      keywords: ['christ', 'christ-centered', 'faith', 'faith-based']
    }
  ];
  selectedServices: any = [];

  demographics = new FormControl();
  demographicList = [
    {
      label: "Adolescent",
      keywords: ['adolescent'],
    },
    {
      label: "Adult",
      keywords: ['adult'],
    },
    {
      label: "Female",
      keywords: ['female', 'women', 'woman'],
    },
    {
      label: "Male",
      keywords: ['male'],
    },
    {
      label: "Women and Children/Pregnant",
      keywords: ["woman", "children", "women", "child"]
    }
  ];
  selectedDemographics: any = [];

  treatmentType = '3'
  treatmentType$ = new BehaviorSubject<TreatmentType>(TreatmentType.Substance);
  county = new BehaviorSubject<string>('');

  isLoading = false;
  hasSearch = false;
  disableDeviceLocationToggle = false;
  @ViewChild(MatAccordion)
  accordion!: MatAccordion;
  constructor(
    private apiClient: ApiService,
    public dialog: MatDialog,
  ) { }

  ngOnInit(): void {
    Geolocation.checkPermissions().then(permissionstatus => {
      if (permissionstatus.location = 'denied') {
        this.canUseDeviceLocation = false;
      }
    });
  }

  onPageChange(pageEvent: PageEvent) {
    this.pageSize = pageEvent.pageSize;
    const pageStart = pageEvent.pageIndex * this.pageSize;
    const pageEnd = Math.min(this.length, this.pageSize + pageStart);

    this.displayedTreatmentProviderList$.next(this.treatmentProviderList$.value.slice(pageStart, pageEnd));
  }

  onSelectEvent(event: any) {
    this.clearFilters();
  }

  getProvidersNearAddress(address: string) {
    const searchFilter: ITreatmentSearchQuery = {
      Address: address,
      Latitude: null,
      Longitude: null,
      Range: Number(this.range),
      TreatmentType: Number(this.treatmentType),
      Demographics: this.selectedDemographics.map((x: Demographic) => x.label),
      ServicesOffered: this.selectedServices.map((x: ServiceOffered) => x.label)
    }

    this.apiClient.searchTreatmentProviders(searchFilter)
      .subscribe(list => this.filterProviders(list),
        () => {
          this.isLoading = false;
          this.hasSearch = true;
        });

    this.accordion.closeAll();
  }

  getProvidersNearPosition(position: Position) {
    const searchFilter: ITreatmentSearchQuery = {
      Address: '',
      Latitude: position.coords.latitude,
      Longitude: position.coords.longitude,
      Range: Number(this.range),
      TreatmentType: Number(this.treatmentType),
      Demographics: this.selectedDemographics.flatMap((x: Demographic) => x.label),
      ServicesOffered: this.selectedServices.flatMap((x: ServiceOffered) => x.label)
    };

    this.apiClient.searchTreatmentProviders(searchFilter).subscribe(list => this.filterProviders(list),
      () => {
        this.isLoading = false;
        this.hasSearch = true;
      });


    this.accordion.closeAll();
  }

  filterProviders(providers: ITreatmentProvider[]) {
    this.isLoading = false;
    this.hasSearch = true;

    let filteredList = providers;

    if (this.selectedDemographics?.length > 0) {
      const keywords = this.selectedDemographics.flatMap((x: Demographic) => x.keywords);
      console.log("demographics: ", keywords)
      filteredList = filteredList.filter((p: ITreatmentProvider) => this.CheckServicesOfferedForKeywords(p.servicesOffered, keywords));
    }

    console.log("filtered list: ", filteredList)
    console.log("\n");
    if (this.selectedServices?.length > 0) {

      const keywords = this.selectedServices.flatMap((x: ServiceOffered) => x.keywords);
      console.log("services offered: ", keywords);
      filteredList = filteredList.filter(p => this.CheckServicesOfferedForKeywords(p.servicesOffered, keywords));
    }

    console.log("filtered list: ", filteredList)
    this.treatmentProviderList$.next(filteredList);
  }

  // GetDistance(x1: number, y1: number, x2: number, y2: number) {
  //   return Math.sqrt(Math.pow((x2 - x1), 2) + Math.pow((y2 - y1), 2));
  // }

  CheckServicesOfferedForKeywords(ServicesOffered: string, keywords: string[] | any) {
    if (keywords === null || keywords === undefined || keywords.length === 0) return false;
    if (ServicesOffered === '' || ServicesOffered === undefined || ServicesOffered === null) return false;
    let services = words(ServicesOffered);

    services = services.map(toLower)
    console.log("services: ", services);
    console.log("keywords: ", keywords);
    let result = intersection(services, keywords);
    console.log("intersection: ", result);
    let keep = (result.length > 0)
    console.log("keep: ", keep);
    return keep;
  }


  // CheckForDemographics(demographicsString: string, keywords: string[] | any) {
  //   if (keywords === null || keywords === undefined || keywords.length === 0) return false;
  //   if (demographicsString === '' || demographicsString === undefined || demographicsString === null) return false;

  //   for (let index = 0; index < keywords.length; index++) {
  //     const keyword = keywords[index];
  //     if (demographicsString.toLowerCase().replace(/[^a-zA-Z ]/g, "").indexOf(keyword) !== -1) {
  //       return true;
  //     }
  //   }
  //   return false;
  // }
  clearFilters() {
    this.selectedServices = [];
    this.selectedDemographics = [];
    this.services.setValue(null);
    this.demographics.setValue(null);

    this.treatmentProviderList$.next([]);
  }

  async applyFilters() {
    this.isLoading = true;
    if (this.useDeviceLocation) {
      Geolocation.getCurrentPosition().then((position) => {
        this.getProvidersNearPosition(position);
      },
        () => {
          // disable use my device location
          this.useDeviceLocation = false;
          this.disableDeviceLocationToggle = true;
          this.isLoading = false;
        })
    } else {
      this.getProvidersNearAddress(this.searchAddress);
    }
  }

  radioChange($event: MatRadioChange) {
    this.clearFilters();
    this.treatmentType = $event.value;

    switch (this.treatmentType) {
      case '1':
        this.serviceList = [
          {
            label: "Residential",
            keywords: ["residential", "ambulatory", "partial", "hospitalization"],
          },
          {
            label: "Outpatient",
            keywords: ['outpatient', 'ambulatory']
          }
        ];

        this.demographicList = [
          {
            label: "Adolescent",
            keywords: ['adolescent', 'child', 'children', 'child/adolescent'],
          },
          {
            label: "Adult",
            keywords: ['adult'],
          }
        ];
        break;
      case '2':
        this.serviceList = [
          {
            label: "Co-occuring Care",
            keywords: ["medically", "monitered", "hiv", "pregnant", "co-occuring"],
          },
          {
            label: "Residential",
            keywords: ["residential", "ambulatory", "partial", "hospitalization"],
          },
          {
            label: "Outpatient",
            keywords: ['outpatient', 'ambulatory']
          },
          {
            label: "Detox",
            keywords: ['detox']
          },
          {
            label: "MAT",
            keywords: ['medically', 'monitered', 'opioid', 'maintenance']
          },
          {
            label: "Recovery Housing",
            keywords: ['housing']
          },
          {
            label: "Faith Based Services",
            keywords: ['christ', 'christ-centered', 'faith', 'faith-based']
          }
        ];

        this.demographicList = [
          {
            label: "Adolescent",
            keywords: ['adolescent'],
          },
          {
            label: "Adult",
            keywords: ['adult'],
          },
          {
            label: "Female",
            keywords: ['female'],
          },
          {
            label: "Male",
            keywords: ['male'],
          },
          {
            label: "Women and Children",
            keywords: ["woman", "children", "women", "child"]
          }
        ];
        break;
      case '3':
        this.serviceList = [
          {
            label: "Co-occuring Care",
            keywords: ["medically", "monitered", "hiv", "pregnant", "co-occuring"],
          },
          {
            label: "Residential",
            keywords: ["residential", "ambulatory", "partial", "hospitalization", "narr"],
          },
          {
            label: "Outpatient",
            keywords: ['outpatient', 'ambulatory']
          },
          {
            label: "Detox",
            keywords: ['detox']
          },
          {
            label: "MAT",
            keywords: ['medically', 'monitered', 'opioid', 'maintenance', 'therapy']
          },
          {
            label: "Recovery Housing",
            keywords: ['housing']
          },
          {
            label: "Faith Based Services",
            keywords: ['christ', 'christ-centered', 'faith', 'faith-based']
          }
        ];

        this.demographicList = [
          {
            label: "Adolescent",
            keywords: ['adolescent'],
          },
          {
            label: "Adult",
            keywords: ['adult'],
          },
          {
            label: "Female",
            keywords: ['female', "woman", "women"],
          },
          {
            label: "Male",
            keywords: ['male'],
          },
          {
            label: "Women and Children",
            keywords: ["woman", "children", "women", "child"]
          }
        ];
        break;
      default:
        break;
    }
    this.treatmentType$.next($event.value);
  }

  openProviderDialog(item: ITreatmentProvider) {
    this.dialog.open(TreatmentLocatorDialog, {
      data: item
    });
  }
}



