import { Injectable } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { JoyrideService } from 'ngx-joyride';
import { SurveyModalComponent } from '../landing/modals/survey-modal/survey-modal.component';
import { Storage } from '@capacitor/storage';

@Injectable({
  providedIn: 'root'
})
export class TutorialService {

  constructor(
    private readonly joyrideService: JoyrideService,
    public dialog: MatDialog,
    private router: Router
  ) { }

  openTutorial() {
    this.router.navigate(["/"]);
    this.joyrideService.startTour(
      {
        steps: [
          'firstStep',
          'substance-use',
          'mental-health',
          'prevention',
          'treatmentLocator',
          'helplines',
          'search@search'
        ],
        showPrevButton: false,
        stepDefaultPosition: 'top',
        themeColor: '#212f23',
        showCounter: false
      }
    ).subscribe({
      next: () => { },
      complete: async () => {
        this.setShowTutorial();
        this.router.navigate(["/"]);
        const { value } = await Storage.get({ key: 'surveyHasBeenShown' });
        if (!value) {
          let dialogRef = this.dialog.open(SurveyModalComponent, {
            maxWidth: '100%',
            maxHeight: '100%',
            panelClass: 'fullscreen-dialog'
          });
          dialogRef.afterClosed().subscribe(() => {
            this.setSurveyShown();
          });
        }
      }
    });
  }

  setShowTutorial = async () => {
    await Storage.set({
      key: 'tutorialHasBeenShown',
      value: 'true',
    });
  };

  setSurveyShown = async () => {
    await Storage.set({
      key: 'surveyHasBeenShown',
      value: 'true',
    });
  };
}
