import { animation, style, animate } from "@angular/animations";

export const fadeIn = animation([
  style({ opacity: 0 }), // start state
  animate('{{time}}', style({ opacity: 1 }))
]);

export const fadeOut = animation([
  animate('{{time}}', style({ opacity: 0 }))
]);

export const slideIn = animation([
  style({ opacity: 0, transform: 'translateX(-100%)' }),
  animate('{{time}}', style({ opacity: 1, transform: 'translateX(0)' }))
]);

export const slideOut = animation([
  style({ opacity: 1, transform: 'translateX(0)' }),
  animate('{{time}}', style({ opacity: 0, transform: 'translateX(100%)' }))
]);

export const slideIn2 = animation([
  style({ opacity: 0, transform: '-translateX(100%)' }),
  animate('{{time}}', style({ opacity: 1, transform: 'translateX(0)' }))
])

export const slideOut2 = animation([
  style({ opacity: 1, transform: 'translateX(0)' }),
  animate('{{time}}', style({ opacity: 0, transform: 'translateX(100%)' }))
])
