import { ITreatmentProvider } from "./TreatmentProviders.models";

export class TreatmentProviderUtils {
  static getAddressString(data: ITreatmentProvider) {
    let value = "";
    if (data.address) {
      value += data.address;
    }
    if (data.city) {
      value += " ";
      value += data.city;
    }
    if (data.state) {
      if (data.city) value += ", ";
      else value += " ";
      value += data.state;
    }
    if (data.zip) {
      value += " ";
      value += data.zip;
    }
    return value;
  }
}
