import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { SearchService } from './search.service';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SearchComponent implements OnInit {
  searchResults$: Observable<any> = new Observable(null);

  constructor(
    public searchService: SearchService,
    private router: Router,
    // private route
  ) { }

  ngOnInit(): void {
    // this.headerService.hideHeader();
    let routes = this.router.config[0];
    let routeData: any = routes.children.map(c => c.data);

    this.searchResults$ = this.searchService.searchResults$.pipe(
      map(result => {
        let mapResult = result.map(x => {
          let r = routeData.find((rd: { path: any; }) => rd.path == x.key);
          // console.log(x.key, " matching route data: ", r);
          if(!r) console.log(x.key);
          return r;
        });

        return mapResult;
      })
    )
  }

  public onInput(event: any) {
    // this pushes the input value into the service's Observable.
    this.searchService.searchTerm.next(event.target.value);
  }
}
