import { Component } from '@angular/core';
import { SplashScreen } from '@capacitor/splash-screen';
import { JoyrideService } from 'ngx-joyride';
import { Storage } from '@capacitor/storage';
import { MatDialog } from '@angular/material/dialog';
import { SurveyModalComponent } from './landing/modals/survey-modal/survey-modal.component';
import { Router } from '@angular/router';
import { WhatsNewModalComponent } from './whats-new-modal/whats-new-modal.component';
import { TutorialService } from './services/tutorial.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'Treatment-Finder-Client';

  constructor(
    public dialog: MatDialog,
    private readonly tutorialService: TutorialService,
    private router: Router
  ) {}

  async ngOnInit(): Promise<void> {
    let { value } = await Storage.get({ key: 'tutorialHasBeenShown' });
    if (!value) {
      await this.router.navigate(["/"]).then(() => {

        this.openTutorial();
      })

    } else {
      let { value } = await Storage.get({ key: 'whatsNewModalHasBeenShown' });
      if(!value) {
        await this.showWhatsNewModal()
      }
    }
  }

  async openTutorial() {
    this.tutorialService.openTutorial();
  }

  showWhatsNewModal = async () => {
    this.dialog.open(WhatsNewModalComponent, {
      maxWidth: '100%',
      maxHeight: '100%',
      panelClass: 'fullscreen-dialog'
    }).afterOpened().subscribe(async () => {
      await Storage.set({
        key: 'whatsNewModalHasBeenShown',
        value: 'true',
      });
    })
  };
}
