import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { TreatmentProviderUtils } from 'src/app/models/TreatmentProvider.Utils';
import { ITreatmentProvider } from 'src/app/models/TreatmentProviders.models';

@Component({
  templateUrl: './treatment-locator-dialog.component.html'
})
export class TreatmentLocatorDialog implements OnInit {
  addressString = '';
  constructor(@Inject(MAT_DIALOG_DATA) public data: ITreatmentProvider) { }

  ngOnInit(): void {
    this.addressString = TreatmentProviderUtils.getAddressString(this.data);
  }
}
