import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { JoyrideService } from 'ngx-joyride';
import { SurveyModalComponent } from 'src/app/landing/modals/survey-modal/survey-modal.component';
import { Storage } from '@capacitor/storage';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { filter, startWith } from 'rxjs/operators';
import { TutorialService } from 'src/app/services/tutorial.service';

export interface HeaderViewModel {
  routerLink?: string,
  pageTitle?: string,
  pageTitleClasses?: string,
  headerClasses?: string,
  showHeader: boolean,
  showAlert: boolean
}

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  host: {
    "(window:click)": "onClick()"
  }
})
export class HeaderComponent implements OnInit {
  connectALLogo = "../../../assets/ConnectAL_Logo.png"
  logo = "../../../assets/icon.gif";
  seal = "../../../assets/seal_clear.png";
  nav_links = [
    { label: "Substance Use", route: "/substance-use" },
    { label: "Mental Health", route: "/mental-health" },
    { label: "Prevention", route: "/prevention" },
    { label: "Service Locator", route: "/treatment-locator" },
    { label: "About", route: '/about' },
    { label: "Search", route: "/search" },
  ];

  menu_open = false;

  data = new BehaviorSubject<HeaderViewModel>({
    showHeader: false,
    showAlert: false,
  } as HeaderViewModel);

  headerViewModel$ = this.data.asObservable();
  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private tutorialService: TutorialService
  ) { }

  async ngOnInit(): Promise<void> {
    console.log("activated route: ", this.activatedRoute.snapshot)

    this.router.events.pipe(
      filter((event) => event instanceof NavigationEnd),
      startWith(this.router)
    ).subscribe((e) => {
      let currentRoute = this.router.routerState.root.firstChild;

      while (currentRoute.firstChild) {
        currentRoute = currentRoute.firstChild;
      }

      this.data.next(currentRoute.snapshot.data as HeaderViewModel);
    });

  }

  doNothing() {

  }

  openTutorial() {
    this.tutorialService.openTutorial();
  }

  toggleMenuOpen($event: any) {
    $event.stopPropagation();
    this.menu_open = !this.menu_open;
  }

  onClick() {
    this.menu_open = false;
  }
}
