import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Counties } from 'src/assets/data/Counties';
import { environment } from 'src/environments/environment';
import { RRCConfitmationDialog } from '../telehealth-form/telehealth-form.component';
import { ErrorDialogComponent } from '../shared/modal/error-dialog/error-dialog.component';

@Component({
  templateUrl: './speaker-request-form.component.html',
  styleUrls: ['./speaker-request-form.component.scss']
})
export class SpeakerRequestFormComponent implements OnInit {
  connectALLogo = "/assets/telehealth/ConnectAL_Horizontal_Color.png";
  // ALDMH = "/assets/ADMH_LOGO_TRANPARENT.png";
  ALDMH = "/assets/ADMH_WHITE.png"
  vitallogo = "/assets/telehealth/VitAL_White.png";
  aphlogo = "/assets/telehealth/Alabama Public Health.png";
  crisisCenterLogo = "/assets/telehealth/Crisis Center.png";

  // Name(required)
  // Working phone number(required)
  // Currently using what substance?(required)
  // Currently in what county?(required)
  countyOptions = Counties;

  //
  speakerRequestForm = new FormGroup({
    email: new FormControl('', [Validators.required]),
    date: new FormControl('', [Validators.required]),
    topic: new FormControl('', [Validators.required]),
    county: new FormControl('', [Validators.required]),
    agency: new FormControl('', [Validators.required]),
    communityAffiliation: new FormControl('', [Validators.required]),
    targetAudience: new FormControl('', [Validators.required]),
  });

  constructor(
    private dialog: MatDialog,
    private httpClient: HttpClient
  ) { }

  ngOnInit(): void {
  }

  onSubmit(): void {
    let rawForm = this.speakerRequestForm.value;

    this.httpClient.post(`${environment.apiUrl}/form/training`, rawForm)
      .subscribe(result => {
        this.speakerRequestForm.reset();
        this.dialog.open(SpeakerRequestConfitmationDialog).afterClosed();
      },
      error => {
        this.dialog.open(ErrorDialogComponent, {
          data: {
            message: "Error creating speaker request. Please try again later."
          }
        })
      })
  }
}


@Component({
  selector: 'speaker-request-confirmation-dialog',
  template: `
    <h2 mat-dialog-title>Thank You!</h2>
    <mat-dialog-content>
      <p>
        Thank you for contacting the Connect Alabama. A Certified Educational Specialist will contact you soon to schedule aspeaker. If this email is received after hours, someone will contact you the following day during regular business hours.
      </p>
    </mat-dialog-content>

    <mat-dialog-actions>
        <button mat-button mat-dialog-close>Ok</button>
    </mat-dialog-actions>
  `,
})
export class SpeakerRequestConfitmationDialog implements OnInit {


  fromWebPage!: string;
  fromWebDialog!: string;

  constructor(
    public dialogRef: MatDialogRef<SpeakerRequestConfitmationDialog>
  ) { }

  ngOnInit(): void { }

  modalDialogYes() {
    this.dialogRef.close({ event: "yes-prop", data: this.fromWebDialog });
  }
  modalDialogNo() {
    this.dialogRef.close({ event: "no-prop", data: this.fromWebDialog });
  }
  modalDialogMaybe() {
    this.dialogRef.close({ event: "maybe-prop", data: this.fromWebDialog });
  }
}
